import React from "react";
import "./SidebarRow.css";
import { Avatar } from "@mui/material";

export default function SidebarRow({ src, Icon, title }) {
  return (
    <div className="sidebarRow">
      {src && <Avatar src={src} className="avatar"/>}
      {Icon && <Icon />}
      <h4>{title}</h4>
    </div>
  );
}
