import { useEffect } from "react";
import { Icon_Ellipsis, Icon_Share, Icon_Smile } from "../icons/fontAwesome/FontAwesome";
import LabelCircle from "../labels/labelCircle/LabelCircle";
import "./Message.css";

function Message({ isMe, avatarSender, component_contentCenter = [] }) {
  useEffect(() => {
    // component_contentCenter = component_contentCenter.map((el, idx) => {
          
    // });

  }, []);
  return (
    <div className="container-message">
      <div className="main-message">
        <div className="body-message">
          <div className="header-message"></div>
          <div className={"content-message" + " " + (isMe ? "isMe" : "")}>
            <div className="contentLeft-message">
              <LabelCircle urlImg={avatarSender} />
            </div>
            <div className="contentCenter-message">
              {component_contentCenter.map((el, idx) => {
                return (
                  <>
                    <div className="sessionContent-message">
                      {Object.entries(el).map((el) => {
        if (el[1] && el[0] !== "time") {
          switch (el[0]) {
            case ("image"):
            return <img src={el[1]}></img>;
            case "text":
            return el[1];
            case "video":
            return <video src={el[1]} controls autoPlay>
              {/* <source src={el[1]} type="video/*"></source> */}
            </video>;
            case "audio":
            return <audio src={el[1]} controls autoPlay></audio>;
            case "reply":
              // var value =el[1]['reply']
            return <div>{el[1]['reply']}</div>;
            default:
              break;
          }
        }
      })}
                      <div className="opt_sessionContent-message">
                        <Icon_Smile />
                        <Icon_Ellipsis />
                        <Icon_Share />
                      </div>
                    </div>
                  </>
                );
              })}
            </div>
            <div className="contentRight-message">{""}</div>
          </div>
          <div className="footer-message"></div>
        </div>
      </div>
    </div>
  );
}
export default Message;
