import PopUpHeader from "../PopUpHeader";
import HeaderSpaceBetween from "../../../../parts/subHeaders/headerSpaceBetween/HeaderSpaceBetween";
import Link_ from "../../../../parts/links/link/Link";
import FormSearch from "../../../../parts/inputs/forms/formSearch/FormSearch";
import { Icon_Ellipsis,Icon_Pen_Square } from "../../../../parts/icons/fontAwesome/FontAwesome";
import ItemOpt from "../../../../parts/item/itemOpt/ItemOpt";
import LabelCircle from "../../../../parts/labels/labelCircle/LabelCircle";
import { useStore } from "../../../../../store";
import { actions } from "../../../../../store";
import PopUpMessenger from "../../popupMessenger/PopUpMessenger";
import { content_sessionMessage, contentPopUpMessenger } from "../../popupMessenger/PopUpMessenger";
import { useEffect,useContext, useRef } from "react";
import { dateTo_textAgo } from "../../../../../store/functions";
import { Context_Account,Context_PopUpMessengers } from "../../../../../store/Context";
import { HOST_SERVER } from "../../../../../config";



function PopupMessageHeader({
  listChat=[]
})
{
  const [state, dispatch] = useStore();
  const handleClick = ({data_Chat}) => {
    fetch(HOST_SERVER+`/chat/getDetailChat?_id=${JSON.stringify(data_Chat._id)}`,{
      credentials:'include',
      method:'GET'
      ,
    })
    .then(res=>res.text()).then(dataJson=>{
      var data= JSON.parse(dataJson)

        dispatch(
          actions.add_popup_messenger(
            <PopUpMessenger idChat={data_Chat._id}
                avatarChat={data_Chat.avatarChat}
                nameChat={data_Chat.nameChat}
                contentsPopUpMessenger={
                  data.content_messages.map(el=>{
                  return new contentPopUpMessenger({
                    isMe : state.account.slug_personal==el.slug_sender,
                    avatarSender:el.avatar_account
                    ,nameSender:el.name_sender
                    ,content_sessionMessage:
                      el.session_messages.map(session_message=>{
                      return  new content_sessionMessage({
                          text:session_message.text,
                          audio:session_message.audio,
                          image:session_message.image,
                          video:session_message.video,
                          reply:session_message.reply,
                          document:session_message.document,
                          time:session_message.time_send,
                        })
                      })
                    
                  })
                })
                
                }
                
            />
          )
        );
      
    })
    
  }  
  useEffect(()=>{
    console.log(listChat[0].lastSessionMessage.time_send,new Date(listChat[0].lastSessionMessage.time_send).toJSON());
      },[])

    return (
        <PopUpHeader
                      isActive={true}
                      header={
                        <>
                          <HeaderSpaceBetween
                            bodyLeft={<h1>Chat</h1>}
                            bodyRight={[
                                <Icon_Ellipsis />
                              ,<Icon_Pen_Square />
                              
                            ].map((el,idx) => {

                              return <LabelCircle key={idx} el_Icon={el} />;
                            })}
                          />
                          <FormSearch placeholder_text={"Search on Message"} />
                        </>
                      }
                      footer={
                        <Link_ text={"See all in mess"} isUnderline={true} />
                      }
                      body={
                        <>
                          {listChat.map((el, idx) => {
                            console.log(el);
                            return (
                              <ItemOpt
                                handleClick={()=>{
                                  handleClick({data_Chat:el})
                                }}
                                children_centerItemOpt={
                                  <>
                                    <b>{el.nameChat}</b>
                                    <div>
                                      <span>{isMeSender({
                                        slug_me: state.account.slug_personal,
                                        name_sender:el.name_sender,
                                        slug_sender: el.slug_sender
                                      })}</span>
                                      {
                                        shortLassSessionMess(el.lastSessionMessage)
                                      }
                                      <span>{dateTo_textAgo(new Date(el.lastSessionMessage.time_send))}</span>
                                    </div>
                                  </>
                                }
                                component_Left={
                                  <>
                                    <LabelCircle urlImg={el.avatarChat} />
                                    {/* <b>{el.name_account}</b>  */}
                                    {/* <div><span>You: </span> A zo what sup men <span>15 minutes ago </span></div> */}
                                  </>
                                }
                                component_Right={
                                  el.isTurnOffNotificationMess ? (
                                    <i className="fa-solid fa-bell-slash"></i>
                                  ) : (
                                    ""
                                  )
                                }
                                key={idx}
                              />
                            );
                          })}
                        </>
                      }
                    />
    )
}
export const shortLassSessionMess=(lastSessionMessage)=>{
  var result;
  Object.entries(lastSessionMessage).forEach(([key,value])=>{
    if(value)
    {
      console.log(value);
      switch (key) {
        case ("image"):
        result= 'Send image'
        break;
        case "text":
        result= ` ${value} `
        break;
        case "video":
        result= ' Send Video '
        break;
        case "audio":
        result= <audio src={value} controls autoPlay></audio>;
        break;
        case "reply":
          // var value =el[1]['reply']
        result= <div>{value['reply']}</div>;
        default:
          break;
      }
    }
    else
    {
      console.log(key);
    }
    
  })
return result
  
}
export function isMeSender({slug_me,slug_sender,name_sender}){
  console.log(slug_me,slug_sender);
  if(slug_me==slug_sender)
  {
    return ' You '
  }
  else
  {
    var shortName = name_sender.split(' ')
    return `${shortName[shortName.length-1]} :`;
  }
}
export default PopupMessageHeader