import { useEffect, useState, useRef } from "react";
import "./PopupNotificationHeader.css";
import PopUpHeader from "../PopUpHeader";
import ItemOpt from "../../../../parts/item/itemOpt/ItemOpt";
import LabelCircle from "../../../../parts/labels/labelCircle/LabelCircle";
import HeaderSpaceBetween from "../../../../parts/subHeaders/headerSpaceBetween/HeaderSpaceBetween";
import ButtonNormal from "../../../../parts/buttons/buttonNormal/ButtonNormal";
var listItemMessPopUpHeader = [
    {
      urlImg_avatar:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT8NwAvsYy2gi0bRkHYkSjNpWkKRJ5bqzZ-Eg&usqp=CAU",
      name_account: "Edegewa Conan",
      isTurnOffNotificationMess: true
    }];
function PopupNotificationHeader({
  isActive,
  header='',
  body='',
  footer='',
  codeLabelPopUP
  ,
  workMount = () => {},
  workUnMount = () => {},
}) {
  console.log("PopupNotificationHeader() render");
  console.groupEnd("PopupNotificationHeader");
  return (
    <PopUpHeader
      isActive={true}
      header={
        <>
          <HeaderSpaceBetween
            bodyLeft={<h1>Notification</h1>}
            bodyRight={[
              ,
              <i className="fa-solid fa-ellipsis"></i>,
            ].map((el) => {
              return <LabelCircle el_Icon={el} />;
            })}
          />
        </>
      }
      body={
        <>
          {listItemMessPopUpHeader.map((el, idx) => {
            return (
              <ItemOpt
                handleClick={el.handleClick}
                component_Left={
                  <>
                    <LabelCircle urlImg={el.urlImg_avatar} />
                    {el.name_account}
                  </>
                }
                // children_centerItemOpt={el.name_account}
                component_Right={
                  el.isTurnOffNotificationMess ? (
                    <i className="fa-solid fa-bell-slash"></i>
                  ) : (
                    ""
                  )
                }
                component_Sub={
                  <>
                    <ButtonNormal isNo={false} textBtn={'Ok'} />
                    <ButtonNormal isNo={true} textBtn={'Refuse'} />
                  </>
                }
                key={idx}
              />
            );
          })}
        </>
      }
    />
  );
}
export default PopupNotificationHeader;
