import { useEffect, useState, useRef, useContext, createContext } from "react";
// import { CreateContext } from "../header/Header";
export const CreateContext = createContext
function Main_handleClickOutSite(initStateActive){
  // var [isShow,set_isShow] = useContext(CreateContext)
  const ref = useRef(null);
  const [active,setActive] = useState(initStateActive);
  
  const handleClickOutSite = (event)=>{
    if(ref.current && !ref.current.contains(event.target))
    {
      // var div =Tmp_add_popup_content(<div>Alo</div>) ;
      setActive(active=>
        {
          return !active}
          );
    }
  }
  useEffect(()=>{
    document.addEventListener('mousedown', handleClickOutSite)
    return ()=>{document.removeEventListener('mousedown', handleClickOutSite)}
  },[ref])
  // return {isShow, set_isShow, ref}
  
  return {active, setActive, ref}
};
function Tmp_add_popup_content(component_PopUp) {
  const [isShow, set_isShow] = useState(true);

  return (
    <CreateContext.Provider 
    value={[isShow, set_isShow]}
    // value={''}
    >
      {component_PopUp}
    </CreateContext.Provider>
  );
}
function PopUp_({
children
,isClickOutside=true
}) {


  

    var active_=true;
    var ref_;
    if(isClickOutside)
    {

        const {active, setActive, ref } = Main_handleClickOutSite(true)
        active_= active;
        ref_=ref;
    }
  console.group("PopUp");
  // const ref = useRef(null);
  console.log("PopUp() render");
  console.groupEnd("PopUp");
  return active_ ? (
    
    <div className="container-PopUp" 
    ref={ref_}
    >
      {/* {div} */}
        {children}
    </div>
  ) : (
    ""
  );
}
export default PopUp_;
